<template>
  <div class="home">
    <MglMap
      class="map"
      :accessToken="accessToken"
      :mapStyle="mapStyle"
      @load="onMapLoaded"
    >
      <MglNavigationControl position="top-left"/>
    </MglMap>
  </div>
</template>

<script>
// @ is an alias to /src
import Mapbox from 'mapbox-gl'
import { MglMap, MglNavigationControl } from 'vue-mapbox'
import octoberRailway from '@/assets/october.json'

export default {
  name: 'Home',
  components: {
    MglMap,
    MglNavigationControl
  },

  data: () => ({
    accessToken: 'pk.eyJ1IjoiY2hpZ2EiLCJhIjoiY2tqYnRyN2NzN3B4ZTJxcWpvdmVldGdmYSJ9.mTIH5Hby4yDyU-LLFOY0Fw',
    mapStyle: 'mapbox://styles/mapbox/streets-v11',
    map: null,
    zoom: 4,
    center: [60.703, 83.214],
    bounds: null,
    url: 'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png',
    layers: {
      railAreas: true
    },
    markers: [
      // {
      //   latLng: [60.703, 83.214],
      //   public: {
      //     total: 1000,
      //     empty: 800
      //   },
      //   private: {
      //     total: 1000,
      //     empty: 500
      //   }
      // }
    ]
  }),

  created () {
    this.mapbox = Mapbox
  },

  methods: {
    onMapLoaded: function (event) {
      this.$store.map = event.map
      this.$store.map.on('zoom', () => {
        console.log(this.$store.map.getZoom())
      })

      // set russian language for all labels
      this.$store.map.setLayoutProperty('country-label', 'text-field', ['get', 'name_ru'])
      this.$store.map.setLayoutProperty('state-label', 'text-field', ['get', 'name_ru'])
      this.$store.map.setLayoutProperty('settlement-label', 'text-field', ['get', 'name_ru'])
      this.$store.map.setLayoutProperty('settlement-subdivision-label', 'text-field', ['get', 'name_ru'])
      this.$store.map.setLayoutProperty('airport-label', 'text-field', ['get', 'name_ru'])
      this.$store.map.setLayoutProperty('transit-label', 'text-field', ['get', 'name_ru'])
      this.$store.map.setLayoutProperty('poi-label', 'text-field', ['get', 'name_ru'])
      this.$store.map.setLayoutProperty('water-point-label', 'text-field', ['get', 'name_ru'])
      this.$store.map.setLayoutProperty('water-line-label', 'text-field', ['get', 'name_ru'])
      this.$store.map.setLayoutProperty('natural-point-label', 'text-field', ['get', 'name_ru'])
      this.$store.map.setLayoutProperty('natural-line-label', 'text-field', ['get', 'name_ru'])
      this.$store.map.setLayoutProperty('waterway-label', 'text-field', ['get', 'name_ru'])
      this.$store.map.setLayoutProperty('road-label', 'text-field', ['get', 'name_ru'])

      // add railway areas
      this.$store.map.addSource('rail-areas', {
        type: 'geojson',
        data: require('@/assets/railAreas.json')
      })
      this.$store.map.addLayer({
        id: 'rail-polygons',
        type: 'fill',
        source: 'rail-areas',
        layout: {},
        maxzoom: 6,
        paint: {
          'fill-color': [
            'match',
            ['get', 'code'],
            [10, 24, 61, 83, 96],
            '#ed1b35',
            [28, 41, 63, 88],
            '#44b85c',
            [17, 51, 80, 92],
            '#0078bf',
            [1, 58, 76, 94],
            '#ffcb31',
            /* other */ 'grey'
          ],
          'fill-opacity': [
            'interpolate',
            ['linear'],
            ['zoom'],
            0,
            0.7,
            2,
            0.20,
            5,
            0.20,
            6,
            0
          ]
        }
      }, 'waterway-label')
      this.$store.map.addLayer({
        id: 'rail-borders',
        type: 'line',
        source: 'rail-areas',
        layout: {
          'line-join': 'round',
          'line-cap': 'round'
        },
        minzoom: 5.5,
        paint: {
          'line-color': 'purple',
          'line-width': 2,
          'line-opacity': [
            'interpolate',
            ['linear'],
            ['zoom'],
            5.5,
            0,
            6.5,
            0.4
          ]
        }
      }, 'waterway-label')

      // add stations
      const stationsFeatures = octoberRailway.stations.map(station => {
        const load = Math.random()
        return {
          type: 'Feature',
          properties: {
            load,
            name: station.name
          },
          geometry: {
            type: 'Point',
            coordinates: [station.latLng[1], station.latLng[0]]
          }
        }
      })

      const stationsGeoJSON = {
        type: 'FeatureCollection',
        features: stationsFeatures
      }

      this.$store.map.addSource('stations', {
        type: 'geojson',
        data: stationsGeoJSON
      })
      this.$store.map.addLayer({
        id: 'stations',
        type: 'circle',
        source: 'stations',
        minzoom: 2.8,
        paint: {
          'circle-radius': {
            stops: [
              [3, 2.5],
              [6, 5],
              [12, 7]
            ]
          },
          'circle-color': [
            'interpolate',
            ['linear'],
            ['get', 'load'],
            0,
            'red',
            0.5,
            'yellow',
            1,
            'green'
          ],
          'circle-opacity': [
            'interpolate',
            ['linear'],
            ['zoom'],
            2.8,
            0,
            3,
            1
          ]
        }
      }, 'waterway-label')
    },
    codeToColor (code) {
      switch (code) {
        case 10:
        case 24:
        case 61:
        case 83:
        case 96:
          return '#525252'
        case 28:
        case 41:
        case 63:
        case 88:
          return '#969696'
        case 17:
        case 51:
        case 80:
        case 92:
          return '#cccccc'
        case 1:
        case 58:
        case 76:
        case 94:
          return '#f7f7f7'
        default:
          return 'white'
      }
    }
  },

  computed: {
    options () {
      return {
        onEachFeature: this.onEachFeatureFunction
      }
    },
    onEachFeatureFunction () {
      return (feature, layer) => {
        this.markers.push({
          latLng: layer.getBounds().getCenter(),
          public: {
            total: feature.properties.code * 100,
            empty: Math.floor(feature.properties.code * 100 * Math.random())
          },
          private: {
            total: feature.properties.code * 10,
            empty: Math.floor(feature.properties.code * 10 * Math.random())
          }
        })
        layer.bindTooltip(
          '<div>code:' +
          feature.properties.code +
          '</div><div>name: ' +
          feature.properties.name +
          '</div>',
          { permanent: false, sticky: true }
        )
        layer.setStyle({
          weight: 1.5,
          color: '#5a5a5a',
          opacity: 1,
          fillColor: this.codeToColor(feature.properties.code),
          fillOpacity: 0.5
        })
      }
    }
  }
}
</script>

<style scoped lang="sass">
.map
  position: absolute
  top: 0
  bottom: 0
  width: 100%
</style>
